@import "../../utils/constants";

.project-card {
  display: flex;
  justify-content: normal;
}

.project-card-text {
  line-height: 1.1;
}

.project-card-title {
  font-size: 30px;
}

.project-card-subtitle {
  font-size: 25px;
  margin: 1rem 0 0.5rem 1.5rem;
}

.project-card-details > * {
  font-size: 20px;
  margin: 1rem 0 0.5rem 1.5rem;
}

@media (max-width: $tablet-width)
{
  .project-card-title {
    font-size: 25px;
  }

  .project-card-subtitle {
    font-size: 20px;
  }

  .project-card-details > * {
    font-size: 15px;
  }
}